<template>
  <div id="ani" @click="add()" @mousedown="boom()" @mouseup="up()">

  </div>
</template>

<script>
import {myAnimation} from '../js/myAnimation.js'
import bus from './bus'
export default {
  name: 'ani',
  props: {
   
  },
    data (){
      return {
          timer:null,
          T:2000,
      }
      
  },
  methods: {
     add(){
         this.visi;
     },
     toEle(){
         bus.$emit('message', 'true');
         
        
     },
     boom(){
          //动画
         this.timer=new Date().getTime();
        
     },
     up(){
         new Date().getTime()-this.timer>this.T?this.$options.methods.toEle():null;
     }

  },
  mounted() {
      myAnimation();   
   
  },
}
</script>

